$custom-red: #dd163b;
$custom-blue: #1e81d2;

.nav-container {
  position: absolute;
  height: 120px;
  width: 1920px;
}

// top navbar
.top-navbar {
  position: absolute;
  top: 0;
  height: 40%;
  width: 1920px;

  .logo-section {
    position: absolute;
    left: 0;
    width: 15%;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
  }
}

// bottom navbar
.bottom-navbar {
  position: absolute;
  bottom: 0;
  height: 60%;
  width: 1920px;
  background: rgba(0, 0, 0, 0.6);
  display: flex;

  // ul
  ul {
    height: 100%;
    display: flex;
    align-items: center;
    width: 60%;

    .mainLink {
      width: 140px;
      height: 72px;
      background: transparent;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 10px;
      z-index: 10;
      transform: skew(-30deg);
    }

    .activeLink {
      background: $custom-red;
    }

    li {
      position: relative;
      transition: 0.4s all ease-in-out;
      transform: skew(30deg) !important;

      .fas {
        position: relative;
        top: 1px;
        right: 4px;
      }

      // events dropdwon
      .dropdown-list {
        display: none;
      }

      .show-dropdown-list {
        position: absolute;
        left: -60px;
        height: 310px;
        width: 200px;
        background: transparent;
        display: flex;
        flex-flow: column;
        margin-top: 45px;
        border-bottom: 4px solid $custom-red;
        display: inline-block;
        z-index: 999;

        .li-bg {
          height: 300px;
          width: 100%;
          position: absolute;
          bottom: 0;
          background: rgba(0, 0, 0, 0.7);
          text-align: center;

          & > .dropdown-content {
            position: relative;
            top: 15px;
            margin: 20px 0;
            transform: skew(0deg) !important;
            z-index: 1000;

            &:hover {
              color: $custom-red;
            }

            &:hover a {
              color: $custom-red;
              transition: 0.4s all ease-in-out;
            }
          }
        }
      }

      // standings dropdown
      .standings {
        position: absolute;
        left: -40px !important;
      }

      // community
      .customPos-arrow {
        position: relative;
        left: 2px;
      }
    }
    // end ul
  }

  // searchbar
  .account-search {
    position: absolute;
    height: 100%;
    right: 30px;
    display: flex;
    align-items: center;

    .login,
    .signup {
      position: relative;
      right: 30px;
      transition: 300ms all ease-in-out;

      &:hover {
        color: $custom-red;
      }
    }

    .slash {
      position: relative;
      right: 30px;
    }

    .fa-search {
      cursor: pointer;
    }
  }
}

.search {
  outline: none;
  height: 45px;
  width: 1920px;
  position: absolute;
  transition: 0.5s all ease-in-out;
  transform-origin: 100% top;
  transform: scaleY(0);
  z-index: 999;
  top: 120px;
}

.showSearch {
  outline: none;
  height: 45px;
  width: 1920px;
  position: absolute;
  transition: 0.5s all ease-in-out;
  transform-origin: 100% top;
  transform: scaleY(1);
  z-index: 999;
  top: 120px;
}
